/* Navbar */
.nav-style {
  background-color: #202023;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 110px;
}

.pkdex-logo-style {
  width: 235px;
  background-color: transparent;
  z-index: 100;
}

.react-logo-style {
  width: 150px;
  background-color: transparent;
  position: absolute;
  z-index: 0;
}

.fav-nav-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 25px;
}

.favorite-count {
  color: white;
}

/* Single Search */
.pkmn-data-style {
  text-align: center;
}

.div-search-bar-style {
  position: static;
  display: block;
  flex-direction: column;
  margin: 22px;
  text-align: center;
}

.button-search-bar-style {
  text-align: center;
}

.search-button-style {
  border-radius: 15px;
  height: 33px;
  width: 134px;
}

.single-search-div {
  background-color: rgb(203, 80, 47);
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-top: solid 4px #dedede;
  border-bottom: solid 4px #dedede;
  border-radius: 50px;
  margin: 20px;
}

.single-search-bar {
  height: 25px;
  border: 4px solid gray;
  border-radius: 15px;
  text-align: center;
}

.single-search-img {
  width: 250px;
  margin: 0px auto;
  position: static;
  display: block;
}

.pkdx-single-container-style {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

/* Grid Pok√©dex */
.pkdx-grid-style {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  padding: 0px 10px;
}

.pkdx-container-style {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

/* Pokemon */
.pokemon-body {
  background-color: rgb(203, 80, 47);
  font-size: 10px;
  color: white;
  border-top: solid 4px #dedede;
  border-bottom: solid 4px #dedede;
  border-radius: 50px;
  margin: 20px;
}

/* Pokemon Type Card */
.grass {
  display: flex;
  box-shadow: 4px 4px 4px black;
  border-radius: 25px;
  margin: 15px;
  width: auto;
  padding: 10px;
  background-color: rgb(35, 177, 30);
}

.grass-poison {
  display: flex;
  box-shadow: 4px 4px 4px black;
  border-radius: 25px;
  margin: 15px;
  width: auto;
  padding: 10px;
  background: rgb(35, 177, 30);
  background: linear-gradient(
    175deg,
    rgba(35, 177, 30, 1) 0%,
    rgba(92, 16, 98, 1) 100%
  );
}

.pokemon-image {
  width: 90px;
  height: 90px;
  padding: 15px;
}

.div-pokemon-img {
  padding-left: 0px;
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
}

.card-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-top h3 {
  text-transform: capitalize;
}

.card-bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.pokemon-type {
  display: flex;
}

.pokemon-favorite {
  font-size: 20px;
}

.pokemon-id {
  font-size: 15px;
}

.pokemon-type-text {
  margin-right: 10px;
  text-transform: capitalize;
}

.heart-btn {
  background-color: transparent;
  border: 0px;
}

/* Pagination */
.pagination {
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* Extra */
@media (prefers-reduced-motion: no-preference) {
  .react-logo-style {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
